

var bot = {
    baseURL: "https://communicatorai.herokuapp.com/question",
    state: {},
    rendered: {},
    app: {},
    picker: null,
    messages: {},
    createOption: function(option){
	var chip = document.createElement('div');
	chip.className = 'chip chip-outline';
	var chipLabel = document.createElement('div');
	chipLabel.className = 'chip-label';
	chipLabel.appendChild(document.createTextNode(option));
	chip.appendChild(chipLabel);
	chip.data = option;
	return chip;
    },
    // Update chats from state
    update: function(){
	var self = this;
	if(this.picker){
	    this.picker.destroy();
	    this.picker = null;
	}
	this.state.chatbox.filter((chat)=>{
	    return !self.rendered[chat.timestamp];
	}).forEach((chat)=>{
	    var sorr = 'received'
	    if(chat.author.id !== 0){
		sorr = 'sent';
	    }
	    chat.sorr = sorr;
	    self.addMessage(chat);
	});
	this.setInputOptions(this.state.options);
    },
    sendRemote: function(input){
	var data = {
	    input: input,
	    state: this.state
	};
	var self = this;
	this.app.request.postJSON(this.baseURL,data,function(data,status,xhr){
	    if(status === 200){
		var re = JSON.parse(xhr.response);
		self.state = re.response.state;
		self.update();
	    }
	},function(xhr,status){},'json');
    },
    start: function(app,messages){
	this.app = app;
	this.messages = messages;
	var data = {
	    input: "start",
	    state: {
		action: "start"
	    }
	};
	var self = this;
	this.app.request.postJSON(this.baseURL,data, function(data, status, xhr){
	    if(status === 200){
		var re = JSON.parse(xhr.response);
		self.state = re.response.state;
		self.update();
	    }
	},function(xhr, status){
	},"json");
    },
    optionSelected: function(e){
	window.bot.sendRemote(e.target.innerText);
	document.getElementsByClassName('userselection')[0].innerHTML = '';
    },
    clearInputOptions: function(){
    },
    setInputOptions: function(options){
	var c = document.getElementsByClassName('userselection')[0];
	document.getElementsByClassName('userselection')[0].innerHTML = '';
	if(options.length === 0){
	    document.getElementsByClassName('userinput')[0].style.display='block';
	    document.getElementsByClassName('userselection')[0].style.display = 'none';
	    return;
	}
	var self = this;
	options.map((option)=>{
	    return self.createOption(option);
	}).forEach((op)=>{
	    op.onclick = self.optionSelected;
	    c.appendChild(op)
	});
	document.getElementsByClassName('userselection')[0].style.display = 'block';
	document.getElementsByClassName('userinput')[0].style.display='none';
	/*
	var self = this;
	this.picker = app.f7.picker.create({
	    inputEl: '.userinput',
	    openIn: 'popover',
	    cols: [
		{
		    values: options,
		    displayValues: options,
		}
	    ],
	    on : {
		closed: function(picker,values){
		    var value = picker.value.pop();
		    self.picker.destroy();
		    $(".userinput").hide();
		    self.sendRemote(value);
		},
		change: function(picker,values,displayValues){
		}
	    }
	});*/
    },
    // Render message in chatbox
    addMessage: function(msg){
	this.rendered[msg.timestamp] = msg;
	this.messages.showTyping("Typing..");
	this.messages.hideTyping();
	this.messages.addMessage({
	    text: msg.message,
	    type: msg.sorr,
	    name: msg.author.name,
	    avatar: msg.author.avatar
	},
				 "append",true)
	this.messages.layout();
    },
    send: function(text){
	this.addMessage({
	    message:text,
	    timestamp: Date.now(),
	    sorr: 'sent',
	    type: 'text',
	    author: {
		name: 'Guest',
		avatar: 'https://cdn.framework7.io/placeholder/people-100x100-9.jpg'
	    }});
    }

};
export default bot;
